.clock {
  position: relative;
  width: 500px;
  height: 500px;
  border: 10px solid #f1ac9d;
  border-radius: 50%;
  background-color: #dee2d1;
}

.clock::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #f06966;
}

.clock__seconds,
.clock__minutes,
.clock__hours {
  position: absolute;
  top: 50%;
  left: 50%;
  transform-origin: 50% 100%;
  width: 0;
  height: 0;
  background-color: #6abe83;
}

.clock__seconds {
  margin: -200px -4px 0;
  padding: 200px 4px 0;
  background-color: #79D2B9;
}

.clock__minutes {
  margin: -160px -4px 0;
  padding: 160px 4px 0;
}

.clock__hours {
  margin: -120px -4px 0;
  padding: 120px 4px 0;
}
